'use client';

import React, { useState } from 'react';
import { useMenu, useOrders, MenuItem } from '../../lib/storage';
import POSMenuGrid from './components/POSMenuGrid';
import POSCart from './components/POSCart';
import { useRouter } from 'next/navigation';

interface CartItem extends MenuItem {
    quantity: number;
    notes?: string;
}

export default function POSPage() {
    const router = useRouter();
    const { categories, items } = useMenu();
    const { orders } = useOrders();
    const [cart, setCart] = useState<CartItem[]>([]);
    const [activeTab, setActiveTab] = useState<'menu' | 'cart'>('menu');
    const [waiterAlert, setWaiterAlert] = useState<number | null>(null);
    const [lastOrder, setLastOrder] = useState<any>(null);
    const [toast, setToast] = useState<{ message: string; visible: boolean }>({ message: '', visible: false });

    const showToast = (message: string) => {
        setToast({ message, visible: true });
        setTimeout(() => setToast(prev => ({ ...prev, visible: false })), 2000);
    };

    React.useEffect(() => {
        const calledOrder = orders.find(o => o.waiterCalled);
        if (calledOrder) {
            setWaiterAlert(calledOrder.id);
            const audio = new Audio('https://assets.mixkit.co/active_storage/sfx/2869/2869-preview.mp3');
            audio.play().catch(e => console.log(e));
        } else {
            setWaiterAlert(null);
        }
    }, [orders]);

    const addToCart = (item: MenuItem) => {
        setCart(prev => {
            const existing = prev.find(i => i.id === item.id);
            if (existing) {
                return prev.map(i => i.id === item.id ? { ...i, quantity: i.quantity + 1 } : i);
            }
            return [...prev, { ...item, quantity: 1 }];
        });
    };

    const updateQuantity = (itemId: string, delta: number) => {
        setCart(prev => prev.map(item => {
            if (item.id === itemId) {
                return { ...item, quantity: Math.max(0, item.quantity + delta) };
            }
            return item;
        }).filter(i => i.quantity > 0));
    };

    const updateItemNotes = (itemId: string, notes: string) => {
        setCart(prev => prev.map(item =>
            item.id === itemId ? { ...item, notes } : item
        ));
    };

    const removeItem = (itemId: string) => {
        setCart(prev => prev.filter(i => i.id !== itemId));
    };

    const clearCart = () => {
        setCart([]);
    };

    const lookupCustomer = (mobile: string) => {
        const pastOrder = orders.find(o => o.mobile === mobile);
        return pastOrder ? pastOrder.customer : null;
    };

    const handleLogout = () => {
        if (confirm('Are you sure you want to logout?')) {
            localStorage.removeItem('role');
            localStorage.removeItem('isAdmin');
            router.push('/dashboard');
        }
    };

    return (
        <div className="flex flex-col h-[100dvh] bg-[#0a0a0a] overflow-hidden">
            {/* Top Header */}
            <header className="bg-[#0f0f0f] border-b border-white/10 px-4 py-3 flex justify-between items-center z-30 flex-shrink-0 sticky top-0">
                <div className="flex items-center gap-3">
                    <button
                        onClick={() => router.push('/dashboard')}
                        className="p-2 -ml-2 hover:bg-white/10 rounded-lg text-white/70 md:hidden flex items-center gap-1 transition-colors"
                    >
                        <span className="text-xl">‚Üê</span>
                        <span className="text-sm font-bold">Back</span>
                    </button>
                    <div className="flex items-center gap-3">
                        <div className="w-10 h-10 rounded-xl bg-gradient-to-r from-orange-500 to-red-500 flex items-center justify-center shadow-lg shadow-orange-500/20">
                            <span className="text-xl">ü•ó</span>
                        </div>
                        <div>
                            <h1 className="font-black text-xl bg-gradient-to-r from-orange-400 to-red-400 bg-clip-text text-transparent">POS Terminal</h1>
                            <span className="text-[10px] text-white/40 font-medium uppercase tracking-wider hidden md:block">Oye Chatoro</span>
                        </div>
                    </div>
                </div>
                <div className="flex gap-2">
                    <button
                        onClick={() => {
                            if (confirm('Go back to Dashboard?')) router.push('/dashboard');
                        }}
                        className="hidden md:flex items-center gap-2 px-4 py-2 text-white/60 hover:text-white hover:bg-white/10 rounded-xl font-bold text-sm border border-white/10 transition-all"
                    >
                        ‚Üê Dashboard
                    </button>
                    <button
                        onClick={handleLogout}
                        className="flex items-center gap-2 px-4 py-2 text-red-400 hover:bg-red-500/10 rounded-xl font-bold transition-colors text-sm border border-red-500/20"
                    >
                        <span className="hidden md:inline">Logout</span>
                        <span className="md:hidden">üö™</span>
                    </button>
                </div>
            </header>

            {/* Last Order Confirmation */}
            {lastOrder && (
                <div className="bg-gradient-to-r from-green-500 to-emerald-500 text-white px-4 py-3 flex justify-between items-center animate-in slide-in-from-top z-50 shadow-lg shadow-green-500/30">
                    <div className="flex items-center gap-3">
                        <span className="text-2xl">‚úÖ</span>
                        <div>
                            <div className="font-bold text-lg">Order #{lastOrder.id} Placed!</div>
                            <div className="text-xs opacity-90">{lastOrder.items.length} Items ‚Ä¢ ‚Çπ{lastOrder.total} ‚Ä¢ {lastOrder.type}</div>
                        </div>
                    </div>
                    <button
                        onClick={() => setLastOrder(null)}
                        className="bg-white/20 hover:bg-white/30 px-4 py-1 rounded-lg font-bold text-sm transition-colors"
                    >
                        Dismiss
                    </button>
                </div>
            )}

            {/* Toast Notification */}
            {toast.visible && (
                <div className="fixed bottom-20 left-1/2 -translate-x-1/2 bg-gradient-to-r from-green-500 to-emerald-500 text-white px-6 py-3 rounded-2xl text-sm font-bold shadow-2xl shadow-green-500/30 z-[60] animate-in fade-in slide-in-from-bottom-4 flex items-center gap-2">
                    <span className="text-lg">‚úì</span>
                    {toast.message}
                </div>
            )}

            {/* Waiter Call Alert */}
            {waiterAlert && (
                <div className="bg-gradient-to-r from-red-500 to-rose-500 text-white px-4 py-3 flex justify-between items-center animate-pulse z-50 shadow-lg shadow-red-500/30">
                    <div className="flex items-center gap-3">
                        <span className="text-2xl">üîî</span>
                        <span className="font-bold text-lg">Chef is calling for Order #{waiterAlert}!</span>
                    </div>
                    <button
                        onClick={() => setWaiterAlert(null)}
                        className="bg-white text-red-600 px-4 py-1 rounded-lg font-bold text-sm shadow-lg"
                    >
                        Dismiss
                    </button>
                </div>
            )}

            {/* Main Content */}
            <div className="flex flex-1 overflow-hidden relative">
                {/* Left Panel: Menu Grid */}
                <div className={`flex-1 h-full overflow-hidden ${activeTab === 'menu' ? 'block' : 'hidden'} md:block`}>
                    <POSMenuGrid
                        categories={categories}
                        items={items}
                        onAddToCart={(item) => {
                            addToCart(item);
                            showToast(`Added ${item.name}`);
                        }}
                    />
                </div>

                {/* Right Panel: Cart */}
                <div className={`w-full md:w-[420px] h-full flex-shrink-0 z-20 md:border-l border-white/10 ${activeTab === 'cart' ? 'block' : 'hidden'} md:block flex flex-col`}>
                    <div className="md:hidden p-2 border-b border-white/10 flex items-center gap-2 bg-[#0f0f0f]">
                        <button
                            onClick={() => setActiveTab('menu')}
                            className="flex items-center gap-1 text-sm font-bold text-white/60 px-3 py-2 rounded-lg hover:bg-white/10 transition-colors"
                        >
                            ‚Üê Back to Menu
                        </button>
                    </div>
                    <POSCart
                        cart={cart}
                        onUpdateQuantity={updateQuantity}
                        onRemoveItem={removeItem}
                        onClearCart={clearCart}
                        onLoadCart={setCart}
                        onUpdateItemNotes={updateItemNotes}
                        onLookupCustomer={lookupCustomer}
                        onOrderPlaced={setLastOrder}
                    />
                </div>
            </div>

            {/* Mobile Mini Cart Summary Bar */}
            {cart.length > 0 && activeTab === 'menu' && (
                <div
                    className="md:hidden fixed bottom-[80px] left-4 right-4 bg-gradient-to-r from-orange-500 to-red-500 text-white p-4 rounded-2xl shadow-2xl shadow-orange-500/40 z-50 flex justify-between items-center animate-in slide-in-from-bottom-4 cursor-pointer hover:scale-[1.02] transition-transform"
                    onClick={() => setActiveTab('cart')}
                >
                    <div className="flex items-center gap-4">
                        <div className="bg-white/20 w-12 h-12 rounded-xl flex items-center justify-center backdrop-blur-md">
                            <span className="text-xl">üõçÔ∏è</span>
                        </div>
                        <div className="flex flex-col">
                            <span className="text-xs font-medium opacity-80 uppercase tracking-wider">{cart.reduce((acc, item) => acc + item.quantity, 0)} Items</span>
                            <span className="font-black text-xl">‚Çπ{cart.reduce((acc, item) => acc + (item.price * item.quantity), 0)}</span>
                        </div>
                    </div>
                    <div className="flex items-center gap-2 bg-white text-orange-600 px-5 py-2.5 rounded-xl font-bold text-sm shadow-lg">
                        View Cart <span className="text-lg">‚Üí</span>
                    </div>
                </div>
            )}

            {/* Mobile Bottom Navigation */}
            <div className="md:hidden bg-[#0f0f0f] border-t border-white/10 flex justify-around p-2 z-40 pb-safe">
                <button
                    onClick={() => setActiveTab('menu')}
                    className={`flex flex-col items-center p-3 w-full rounded-xl transition-all ${activeTab === 'menu' ? 'bg-gradient-to-r from-orange-500/20 to-red-500/20 text-orange-400' : 'text-white/40'}`}
                >
                    <span className="text-2xl">üçî</span>
                    <span className="text-xs font-bold mt-1">Menu</span>
                </button>
                <button
                    onClick={() => setActiveTab('cart')}
                    className={`flex flex-col items-center p-3 w-full rounded-xl transition-all relative ${activeTab === 'cart' ? 'bg-gradient-to-r from-orange-500/20 to-red-500/20 text-orange-400' : 'text-white/40'}`}
                >
                    <span className="text-2xl">üõí</span>
                    <span className="text-xs font-bold mt-1">Cart</span>
                    {cart.reduce((acc, item) => acc + item.quantity, 0) > 0 && (
                        <span className="absolute top-2 right-[calc(50%-18px)] bg-gradient-to-r from-orange-500 to-red-500 text-white text-[10px] min-w-[18px] h-[18px] rounded-full flex items-center justify-center px-1 font-bold shadow-lg shadow-orange-500/30">
                            {cart.reduce((acc, item) => acc + item.quantity, 0)}
                        </span>
                    )}
                </button>
            </div>
        </div>
    );
}
