'use client';

import React, { useState } from 'react';
import Image from 'next/image';
import { MenuItem, MenuCategory } from '../../../lib/storage';

interface POSMenuGridProps {
    categories: MenuCategory[];
    items: MenuItem[];
    onAddToCart: (item: MenuItem) => void;
}

export default function POSMenuGrid({ categories, items, onAddToCart }: POSMenuGridProps) {
    const [activeCategory, setActiveCategory] = useState<string>('All');
    const [searchQuery, setSearchQuery] = useState('');
    const [isListening, setIsListening] = useState(false);

    const startVoiceSearch = () => {
        if (typeof window === 'undefined') return;
        const SpeechRecognition = (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;

        if (!SpeechRecognition) {
            alert('Voice search not supported in this browser');
            return;
        }

        const recognition = new SpeechRecognition();
        recognition.continuous = false;
        recognition.interimResults = false;
        recognition.lang = 'en-US';

        setIsListening(true);

        recognition.onresult = (event: any) => {
            const transcriptRaw = event.results[0][0].transcript;
            const transcript = transcriptRaw.replace(/[.,?!]+$/, '').trim();
            setSearchQuery(transcript);

            const match = transcript.match(/(\d+)\s+(.*)/);
            if (match) {
                const qty = parseInt(match[1]);
                const keyword = match[2].toLowerCase();
                const item = items.find(i => i.name.toLowerCase().includes(keyword));
                if (item) {
                    for (let i = 0; i < qty; i++) onAddToCart(item);
                    setSearchQuery('');
                }
            }

            setIsListening(false);
        };

        recognition.onerror = () => setIsListening(false);
        recognition.onend = () => setIsListening(false);

        recognition.start();
    };

    const filteredItems = items.filter(item => {
        const matchesCategory = activeCategory === 'All' || item.categoryId === activeCategory;
        const matchesSearch = item.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
            item.id.toLowerCase().includes(searchQuery.toLowerCase());
        return matchesCategory && matchesSearch && item.status === 'Active';
    });

    return (
        <div className="flex flex-col h-full bg-[#0f0f0f] relative overflow-hidden">
            {/* Animated Background Orbs */}
            <div className="absolute inset-0 pointer-events-none overflow-hidden">
                <div className="absolute top-20 left-20 w-64 h-64 bg-orange-500/10 rounded-full filter blur-[80px] animate-pulse" />
                <div className="absolute bottom-20 right-20 w-48 h-48 bg-red-500/10 rounded-full filter blur-[60px] animate-pulse" style={{ animationDelay: '1s' }} />
            </div>

            {/* Search & Filter Header */}
            <div className="p-4 bg-[#0f0f0f]/90 backdrop-blur-xl border-b border-white/10 sticky top-0 z-10 relative">
                <div className="relative mb-4">
                    <div className="absolute left-4 top-1/2 -translate-y-1/2 w-10 h-10 rounded-full bg-gradient-to-r from-orange-500 to-red-500 flex items-center justify-center shadow-lg shadow-orange-500/20">
                        <svg className="w-5 h-5 text-white" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5"><circle cx="11" cy="11" r="8" /><path d="M21 21l-4.35-4.35" /></svg>
                    </div>
                    <input
                        type="text"
                        placeholder="Search items or say '2 Burger'..."
                        className="w-full p-4 pl-16 pr-14 bg-white/5 border border-white/10 rounded-2xl text-white placeholder-white/40 outline-none focus:border-orange-500/50 focus:bg-white/10 transition-all text-base font-medium"
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                    />
                    <button
                        onClick={startVoiceSearch}
                        className={`absolute right-3 top-1/2 -translate-y-1/2 w-10 h-10 rounded-full flex items-center justify-center transition-all ${isListening ? 'bg-red-500 text-white animate-pulse shadow-lg shadow-red-500/50' : 'bg-white/10 text-white/60 hover:bg-white/20 hover:text-white'}`}
                    >
                        {isListening ? 'üé§' : 'üéôÔ∏è'}
                    </button>
                </div>

                <div className="flex gap-2 overflow-x-auto pb-2 scrollbar-hide">
                    <button
                        onClick={() => setActiveCategory('All')}
                        className={`px-5 py-2.5 rounded-xl text-sm font-bold whitespace-nowrap transition-all ${activeCategory === 'All'
                            ? 'bg-gradient-to-r from-orange-500 to-red-500 text-white shadow-lg shadow-orange-500/30'
                            : 'bg-white/5 border border-white/10 text-white/70 hover:bg-white/10 hover:text-white'
                            }`}
                    >
                        All Items
                    </button>
                    {categories.map(cat => (
                        <button
                            key={cat.id}
                            onClick={() => setActiveCategory(cat.id)}
                            className={`px-5 py-2.5 rounded-xl text-sm font-bold whitespace-nowrap transition-all ${activeCategory === cat.id
                                ? 'bg-gradient-to-r from-orange-500 to-red-500 text-white shadow-lg shadow-orange-500/30'
                                : 'bg-white/5 border border-white/10 text-white/70 hover:bg-white/10 hover:text-white'
                                }`}
                        >
                            {cat.name}
                        </button>
                    ))}
                </div>
            </div>

            {/* Menu Grid */}
            <div className="flex-1 overflow-y-auto p-4 pb-32 md:pb-4 relative">
                <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-3">
                    {filteredItems.map((item, index) => (
                        <button
                            key={item.id}
                            onClick={() => onAddToCart(item)}
                            style={{ animationDelay: `${index * 0.03}s` }}
                            className="animate-in fade-in slide-in-from-bottom-2 bg-white/5 backdrop-blur-sm border border-white/10 p-3 rounded-2xl hover:border-orange-500/50 hover:bg-white/10 hover:scale-[1.02] transition-all text-left group flex flex-col h-full relative overflow-hidden"
                        >
                            {/* Hover Glow */}
                            <div className="absolute inset-0 bg-gradient-to-br from-orange-500/0 to-red-500/0 group-hover:from-orange-500/10 group-hover:to-red-500/10 transition-all duration-500 rounded-2xl" />

                            <div className="aspect-[4/3] bg-black/30 rounded-xl mb-3 overflow-hidden relative">
                                {item.image ? (
                                    <Image
                                        src={item.image}
                                        alt={item.name}
                                        fill
                                        className="object-cover group-hover:scale-110 transition-transform duration-500"
                                        sizes="(max-width: 768px) 50vw, 25vw"
                                    />
                                ) : (
                                    <div className="w-full h-full flex items-center justify-center text-3xl">ü•ò</div>
                                )}
                                <div className={`absolute top-2 right-2 w-6 h-6 rounded-lg flex items-center justify-center text-[10px] font-bold ${item.veg ? 'bg-green-500/20 border border-green-500 text-green-400' : 'bg-red-500/20 border border-red-500 text-red-400'}`}>
                                    ‚óè
                                </div>
                                {item.isFeatured && (
                                    <div className="absolute top-2 left-2 bg-gradient-to-r from-orange-500 to-red-500 px-2 py-0.5 rounded-lg text-[9px] font-bold text-white uppercase tracking-wider shadow-lg">
                                        ‚≠ê Best
                                    </div>
                                )}
                            </div>
                            <div className="flex-1 flex flex-col justify-between relative z-10">
                                <div>
                                    <h3 className="font-bold text-white text-sm line-clamp-2 mb-1">{item.name}</h3>
                                    <p className="text-xs text-white/40 line-clamp-1">{item.description}</p>
                                </div>
                                <div className="mt-3 flex justify-between items-center">
                                    <span className="font-black text-lg bg-gradient-to-r from-orange-400 to-red-400 bg-clip-text text-transparent">‚Çπ{item.price}</span>
                                    <div className="w-9 h-9 rounded-xl bg-white/10 flex items-center justify-center text-white/50 group-hover:bg-gradient-to-r group-hover:from-orange-500 group-hover:to-red-500 group-hover:text-white group-hover:shadow-lg group-hover:shadow-orange-500/30 transition-all font-bold text-lg">
                                        +
                                    </div>
                                </div>
                            </div>
                        </button>
                    ))}
                </div>
                {filteredItems.length === 0 && (
                    <div className="flex flex-col items-center justify-center h-64 text-white/40">
                        <div className="text-6xl mb-4 animate-pulse">üîç</div>
                        <p className="text-lg font-medium">No items found</p>
                        <p className="text-sm text-white/30">Try a different search</p>
                    </div>
                )}
            </div>
        </div>
    );
}
