'use client';

import React, { useState, useEffect } from 'react';
import Image from 'next/image';
import { MenuItem, createOrder } from '../../../lib/storage';

interface CartItem extends MenuItem {
    quantity: number;
    notes?: string;
}

interface POSCartProps {
    cart: CartItem[];
    onUpdateQuantity: (itemId: string, delta: number) => void;
    onRemoveItem: (itemId: string) => void;
    onClearCart: () => void;
    onLoadCart: (cart: CartItem[]) => void;
    onUpdateItemNotes: (itemId: string, notes: string) => void;
    onLookupCustomer: (mobile: string) => string | null;
    onOrderPlaced?: (order: any) => void;
}

export default function POSCart({ cart, onUpdateQuantity, onRemoveItem, onClearCart, onLoadCart, onUpdateItemNotes, onLookupCustomer, onOrderPlaced }: POSCartProps) {
    const [customerName, setCustomerName] = useState('');
    const [orderType, setOrderType] = useState<'Dine-in' | 'Takeaway'>('Dine-in');
    const [mobileNumber, setMobileNumber] = useState('');
    const [paymentMethod, setPaymentMethod] = useState<'Cash' | 'Online'>('Cash');
    const [isPaid, setIsPaid] = useState(false);
    const [isProcessing, setIsProcessing] = useState(false);

    const [heldOrders, setHeldOrders] = useState<{ id: number; time: string; cart: CartItem[]; customer: string; mobile: string }[]>([]);
    const [showHeldOrders, setShowHeldOrders] = useState(false);

    const [discount, setDiscount] = useState<number>(0);
    const [discountType, setDiscountType] = useState<'amount' | 'percent'>('amount');
    const [showDiscountInput, setShowDiscountInput] = useState(false);

    useEffect(() => {
        const saved = localStorage.getItem('pos_held_orders');
        if (saved) setHeldOrders(JSON.parse(saved));
    }, []);

    const saveHeldOrders = (orders: any[]) => {
        setHeldOrders(orders);
        localStorage.setItem('pos_held_orders', JSON.stringify(orders));
    };

    const handleHoldOrder = () => {
        if (cart.length === 0) return;
        const newHeldOrder = {
            id: Date.now(),
            time: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
            cart,
            customer: customerName || 'Walk-in',
            mobile: mobileNumber
        };
        saveHeldOrders([newHeldOrder, ...heldOrders]);
        onClearCart();
        setCustomerName('');
        setMobileNumber('');
        alert('Order Held Successfully! ‚è∏Ô∏è');
    };

    const handleResumeOrder = (orderId: number) => {
        const orderToResume = heldOrders.find(o => o.id === orderId);
        if (!orderToResume) return;
        if (cart.length > 0 && !confirm('Current cart will be cleared. Continue?')) return;
        onLoadCart(orderToResume.cart);
        setCustomerName(orderToResume.customer !== 'Walk-in' ? orderToResume.customer : '');
        setMobileNumber(orderToResume.mobile);
        saveHeldOrders(heldOrders.filter(o => o.id !== orderId));
        setShowHeldOrders(false);
    };

    const handleDiscardHeldOrder = (orderId: number) => {
        if (!confirm('Delete this held order?')) return;
        saveHeldOrders(heldOrders.filter(o => o.id !== orderId));
    };

    const handleMobileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const val = e.target.value;
        if (/^\d*$/.test(val) && val.length <= 10) {
            setMobileNumber(val);
            if (val.length === 10) {
                const existingCustomer = onLookupCustomer(val);
                if (existingCustomer) setCustomerName(existingCustomer);
            }
        }
    };

    const itemTotal = cart.reduce((sum, item) => sum + (item.price * item.quantity), 0);
    const tax = Math.round(itemTotal * 0.05);
    let discountAmount = discount > 0 ? (discountType === 'amount' ? discount : Math.round((itemTotal + tax) * (discount / 100))) : 0;
    const grandTotal = Math.max(0, (itemTotal + tax) - discountAmount);

    const handlePrintBill = () => {
        const printWindow = window.open('', '', 'width=400,height=600');
        if (!printWindow) return;
        const receiptId = Math.floor(100000 + Math.random() * 900000);
        const html = `
            <html>
                <head>
                    <title>Bill Receipt</title>
                    <style>
                        @page { margin: 0; }
                        body { font-family: 'Courier New', monospace; padding: 20px; width: 300px; margin: 0 auto; color: #000; }
                        .header { text-align: center; margin-bottom: 15px; }
                        .logo { font-size: 24px; font-weight: bold; margin-bottom: 5px; }
                        .address { font-size: 12px; margin-bottom: 5px; }
                        .divider { border-bottom: 1px dashed #000; margin: 10px 0; }
                        .info-row { display: flex; justify-content: space-between; font-size: 12px; margin-bottom: 3px; }
                        .items-header { display: flex; font-size: 12px; font-weight: bold; margin-bottom: 5px; }
                        .col-name { flex: 2; text-align: left; }
                        .col-qty { flex: 0.5; text-align: center; }
                        .col-price { flex: 1; text-align: right; }
                        .item-row { display: flex; font-size: 12px; margin-bottom: 3px; }
                        .totals { margin-top: 10px; }
                        .total-row { display: flex; justify-content: space-between; font-size: 12px; margin-bottom: 3px; }
                        .grand-total { font-size: 16px; font-weight: bold; margin-top: 5px; border-top: 1px dashed #000; padding-top: 5px; }
                        .footer { text-align: center; margin-top: 20px; font-size: 12px; }
                    </style>
                </head>
                <body>
                    <div class="header">
                        <div class="logo">ü•ó Oye Chatoro</div>
                        <div class="address">Abu Road, Rajasthan</div>
                        <div class="address">Ph: +91 95099 13792</div>
                    </div>
                    <div class="divider"></div>
                    <div class="info-row"><span>Date: ${new Date().toLocaleDateString()}</span><span>Time: ${new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</span></div>
                    <div class="info-row"><span>Receipt: #${receiptId}</span><span>Type: ${orderType}</span></div>
                    ${customerName ? `<div class="info-row"><span>Customer: ${customerName}</span></div>` : ''}
                    <div class="divider"></div>
                    <div class="items-header"><span class="col-name">Item</span><span class="col-qty">Qty</span><span class="col-price">Amt</span></div>
                    ${cart.map(item => `<div class="item-row"><span class="col-name">${item.name}</span><span class="col-qty">${item.quantity}</span><span class="col-price">‚Çπ${item.price * item.quantity}</span></div>`).join('')}
                    <div class="divider"></div>
                    <div class="totals">
                        <div class="total-row"><span>Subtotal</span><span>‚Çπ${itemTotal}</span></div>
                        <div class="total-row"><span>Tax (5%)</span><span>‚Çπ${tax}</span></div>
                        ${discount > 0 ? `<div class="total-row"><span>Discount</span><span>-‚Çπ${discountAmount}</span></div>` : ''}
                        <div class="total-row grand-total"><span>TOTAL</span><span>‚Çπ${grandTotal}</span></div>
                    </div>
                    <div class="divider"></div>
                    <div class="footer"><p>Thank you for visiting!</p><p style="font-size: 10px;">Powered by Oye Chatoro POS</p></div>
                    <script>window.onload = function() { window.print(); window.close(); }</script>
                </body>
            </html>
        `;
        printWindow.document.write(html);
        printWindow.document.close();
    };

    const handleCheckout = () => {
        if (cart.length === 0) return;
        setIsProcessing(true);
        setTimeout(() => {
            const newOrder = createOrder({
                customer: customerName || 'Walk-in Customer',
                items: cart.map(i => `${i.name} (${i.quantity}) ${i.notes ? `[${i.notes}]` : ''}`),
                total: grandTotal,
                type: orderType,
                mobile: mobileNumber || undefined,
                paymentStatus: isPaid ? 'Paid' : 'Unpaid',
                paymentMethod: isPaid ? paymentMethod : undefined,
                discount: discount > 0 ? { type: discountType, value: discount, amount: discountAmount } : undefined
            });
            if (onOrderPlaced) onOrderPlaced(newOrder);
            setCustomerName('');
            setMobileNumber('');
            setIsPaid(false);
            setDiscount(0);
            setShowDiscountInput(false);
            onClearCart();
            setIsProcessing(false);
            const audio = new Audio('/sounds/success.mp3');
            audio.play().catch(() => { });
            alert('Order Placed Successfully!');
        }, 800);
    };

    return (
        <div className="flex flex-col h-full bg-[#0f0f0f] relative">
            {/* Animated Background */}
            <div className="absolute inset-0 pointer-events-none overflow-hidden">
                <div className="absolute bottom-20 right-10 w-40 h-40 bg-orange-500/10 rounded-full filter blur-[60px] animate-pulse" />
            </div>

            {/* Held Orders Modal */}
            {showHeldOrders && (
                <div className="absolute inset-0 bg-black/95 backdrop-blur-xl z-50 flex flex-col animate-in slide-in-from-bottom-10">
                    <div className="p-4 border-b border-white/10 flex justify-between items-center">
                        <h3 className="font-bold text-lg text-yellow-400 flex items-center gap-2">
                            ‚è∏Ô∏è Held Orders
                            <span className="bg-yellow-500/20 text-yellow-400 text-xs px-2 py-0.5 rounded-full">{heldOrders.length}</span>
                        </h3>
                        <button onClick={() => setShowHeldOrders(false)} className="text-white/50 hover:text-white p-2 hover:bg-white/10 rounded-full transition-all">‚úï</button>
                    </div>
                    <div className="flex-1 overflow-y-auto p-4 space-y-3">
                        {heldOrders.length === 0 ? (
                            <div className="flex flex-col items-center justify-center h-64 text-white/40">
                                <div className="text-4xl mb-2 animate-pulse">üì≠</div>
                                <p>No held orders</p>
                            </div>
                        ) : (
                            heldOrders.map(order => (
                                <div key={order.id} className="bg-white/5 border border-white/10 rounded-2xl p-4 hover:border-yellow-500/30 transition-all">
                                    <div className="flex justify-between items-start mb-3">
                                        <div>
                                            <div className="font-bold text-white">{order.customer || 'Walk-in'}</div>
                                            <div className="text-xs text-white/40 flex items-center gap-2">
                                                <span>üì± {order.mobile || 'N/A'}</span>
                                                <span>‚Ä¢</span>
                                                <span>üïí {order.time}</span>
                                            </div>
                                        </div>
                                        <div className="text-xs bg-yellow-500/20 text-yellow-400 px-3 py-1 rounded-full font-bold">
                                            {order.cart.reduce((s, i) => s + i.quantity, 0)} Items
                                        </div>
                                    </div>
                                    <div className="text-sm text-white/50 mb-4 line-clamp-1">{order.cart.map(i => i.name).join(', ')}</div>
                                    <div className="flex gap-3">
                                        <button onClick={() => handleResumeOrder(order.id)} className="flex-1 bg-gradient-to-r from-orange-500 to-red-500 text-white py-2.5 rounded-xl text-sm font-bold shadow-lg shadow-orange-500/20">Resume üöÄ</button>
                                        <button onClick={() => handleDiscardHeldOrder(order.id)} className="px-4 bg-white/5 border border-red-500/30 text-red-400 rounded-xl hover:bg-red-500/10 transition-all">üóëÔ∏è</button>
                                    </div>
                                </div>
                            ))
                        )}
                    </div>
                </div>
            )}

            {/* Header */}
            <div className="p-5 border-b border-white/10 flex justify-between items-center z-10 relative">
                <div>
                    <h2 className="font-black text-xl text-white flex items-center gap-2">
                        Current Order
                    </h2>
                    <p className="text-xs text-white/40 font-medium mt-0.5">{cart.length} items in cart</p>
                </div>
                <div className="flex gap-2">
                    {heldOrders.length > 0 && (
                        <button onClick={() => setShowHeldOrders(true)} className="text-xs bg-yellow-500/20 text-yellow-400 border border-yellow-500/30 px-3 py-1.5 rounded-lg font-bold hover:bg-yellow-500/30 transition-all flex items-center gap-1">
                            ‚è∏Ô∏è Held ({heldOrders.length})
                        </button>
                    )}
                    <button onClick={onClearCart} disabled={cart.length === 0} className="text-xs text-red-400 hover:text-red-300 hover:bg-red-500/10 px-3 py-1.5 rounded-lg font-bold transition-all disabled:opacity-50">Clear All</button>
                </div>
            </div>

            {/* Cart Items */}
            <div className="flex-1 overflow-y-auto p-4 space-y-3 relative">
                {cart.length === 0 ? (
                    <div className="flex flex-col items-center justify-center h-full text-white/40">
                        <div className="w-24 h-24 bg-white/5 rounded-full flex items-center justify-center mb-4 animate-pulse">
                            <span className="text-4xl">üõí</span>
                        </div>
                        <p className="font-bold text-lg">Cart is empty</p>
                        <p className="text-sm text-white/30">Select items from menu</p>
                    </div>
                ) : (
                    cart.map((item, index) => (
                        <div key={item.id} style={{ animationDelay: `${index * 0.05}s` }} className="animate-in slide-in-from-right-4 bg-white/5 border border-white/10 p-3 rounded-2xl hover:border-orange-500/30 transition-all relative overflow-hidden group">
                            <div className="absolute inset-0 bg-gradient-to-r from-orange-500/0 to-red-500/0 group-hover:from-orange-500/5 group-hover:to-red-500/5 transition-all" />
                            <div className="flex gap-4 items-start relative z-10">
                                <div className="w-14 h-14 bg-black/30 rounded-xl overflow-hidden flex-shrink-0 relative">
                                    {item.image ? (
                                        <Image src={item.image} alt={item.name} fill className="object-cover" sizes="56px" />
                                    ) : (
                                        <div className="w-full h-full flex items-center justify-center text-xl">ü•ò</div>
                                    )}
                                </div>
                                <div className="flex-1 min-w-0">
                                    <div className="flex justify-between items-start mb-1">
                                        <h4 className="font-bold text-white text-sm truncate pr-2">{item.name}</h4>
                                        <span className="font-black text-orange-400 whitespace-nowrap">‚Çπ{item.price * item.quantity}</span>
                                    </div>
                                    <div className="flex items-center justify-between mt-2">
                                        <div className="flex items-center bg-white/10 rounded-lg p-1 border border-white/10">
                                            <button onClick={() => onUpdateQuantity(item.id, -1)} className="w-7 h-7 flex items-center justify-center bg-white/10 rounded-md text-white hover:bg-white/20 transition-all font-bold text-lg">‚àí</button>
                                            <span className="w-8 text-center font-bold text-sm text-white">{item.quantity}</span>
                                            <button onClick={() => onUpdateQuantity(item.id, 1)} className="w-7 h-7 flex items-center justify-center bg-gradient-to-r from-orange-500 to-red-500 text-white rounded-md shadow-lg shadow-orange-500/20 font-bold text-lg">+</button>
                                        </div>
                                        <div className="flex items-center gap-2">
                                            <button
                                                onClick={() => {
                                                    const note = prompt('Add Note:', item.notes || '');
                                                    if (note !== null) onUpdateItemNotes(item.id, note);
                                                }}
                                                className={`text-xs px-2 py-1 rounded-lg font-medium transition-colors ${item.notes ? 'bg-orange-500/20 text-orange-400 border border-orange-500/30' : 'text-white/40 hover:bg-white/10'}`}
                                            >
                                                {item.notes ? 'üìù' : '+ Note'}
                                            </button>
                                            <button onClick={() => onRemoveItem(item.id)} className="w-7 h-7 flex items-center justify-center text-white/30 hover:text-red-400 hover:bg-red-500/10 rounded-lg transition-all">‚úï</button>
                                        </div>
                                    </div>
                                    {item.notes && (
                                        <div className="mt-2 text-xs text-orange-400 bg-orange-500/10 px-2 py-1 rounded-md border border-orange-500/20">üìù {item.notes}</div>
                                    )}
                                </div>
                            </div>
                        </div>
                    ))
                )}
            </div>

            {/* Checkout Section */}
            <div className="bg-white/5 border-t border-white/10 z-20 relative">
                <div className="p-4 pb-2 grid grid-cols-2 gap-3">
                    <div className="col-span-2 flex bg-white/10 p-1 rounded-xl">
                        <button onClick={() => setOrderType('Dine-in')} className={`flex-1 py-2.5 rounded-lg text-xs font-bold transition-all ${orderType === 'Dine-in' ? 'bg-gradient-to-r from-orange-500 to-red-500 text-white shadow-lg' : 'text-white/50 hover:text-white'}`}>üçΩÔ∏è Dine-in</button>
                        <button onClick={() => setOrderType('Takeaway')} className={`flex-1 py-2.5 rounded-lg text-xs font-bold transition-all ${orderType === 'Takeaway' ? 'bg-gradient-to-r from-orange-500 to-red-500 text-white shadow-lg' : 'text-white/50 hover:text-white'}`}>üõçÔ∏è Takeaway</button>
                    </div>
                    <input type="text" placeholder="Customer Name" className="w-full px-3 py-2.5 bg-white/10 border border-white/10 focus:border-orange-500/50 rounded-xl text-sm text-white placeholder-white/40 outline-none transition-all" value={customerName} onChange={(e) => setCustomerName(e.target.value)} />
                    <input type="tel" placeholder="Mobile No." className="w-full px-3 py-2.5 bg-white/10 border border-white/10 focus:border-orange-500/50 rounded-xl text-sm text-white placeholder-white/40 outline-none transition-all" value={mobileNumber} onChange={handleMobileChange} maxLength={10} />
                </div>

                <div className="p-4 pt-2 space-y-3">
                    <div className="space-y-1">
                        <div className="flex justify-between text-xs text-white/40 px-1"><span>Subtotal</span><span>‚Çπ{itemTotal}</span></div>
                        <div className="flex justify-between text-xs text-white/40 px-1"><span>Tax (5%)</span><span>‚Çπ{tax}</span></div>
                        {discount > 0 && <div className="flex justify-between text-xs text-green-400 font-medium px-1"><span>Discount</span><span>-‚Çπ{discountAmount}</span></div>}
                        <div className="flex justify-between items-center bg-white/10 p-3 rounded-xl border border-white/10 mt-2">
                            <div className="flex flex-col">
                                <span className="text-xs text-white/40 font-medium">Total Amount</span>
                                <span className="text-2xl font-black bg-gradient-to-r from-orange-400 to-red-400 bg-clip-text text-transparent">‚Çπ{grandTotal}</span>
                            </div>
                            <button onClick={() => setShowDiscountInput(!showDiscountInput)} className={`px-3 py-1.5 rounded-lg text-xs font-bold transition-all ${discount > 0 ? 'bg-green-500/20 text-green-400' : 'bg-white/10 text-white/60 hover:bg-white/20'}`}>
                                {discount > 0 ? 'Edit %' : '+ Discount'}
                            </button>
                        </div>
                    </div>

                    {showDiscountInput && (
                        <div className="flex gap-2 animate-in slide-in-from-top-2">
                            <div className="flex bg-white/10 rounded-lg p-1">
                                <button onClick={() => setDiscountType('amount')} className={`px-3 py-1 rounded-md text-xs font-bold transition-all ${discountType === 'amount' ? 'bg-white/20 text-white' : 'text-white/50'}`}>‚Çπ</button>
                                <button onClick={() => setDiscountType('percent')} className={`px-3 py-1 rounded-md text-xs font-bold transition-all ${discountType === 'percent' ? 'bg-white/20 text-white' : 'text-white/50'}`}>%</button>
                            </div>
                            <input type="number" className="flex-1 bg-white/10 border border-white/10 rounded-lg px-3 text-sm text-white outline-none focus:border-orange-500/50" placeholder="Value" value={discount || ''} onChange={(e) => setDiscount(Number(e.target.value))} />
                        </div>
                    )}

                    <div className="grid grid-cols-2 gap-2">
                        <button onClick={() => setIsPaid(!isPaid)} className={`py-3 rounded-xl text-sm font-bold transition-all ${isPaid ? 'bg-green-500/20 border border-green-500/30 text-green-400' : 'bg-white/10 border border-white/10 text-white/50 hover:bg-white/20'}`}>
                            {isPaid ? '‚úÖ Paid' : 'Mark Paid'}
                        </button>
                        {isPaid ? (
                            <div className="flex bg-white/10 p-1 rounded-xl">
                                <button onClick={() => setPaymentMethod('Cash')} className={`flex-1 rounded-lg text-xs font-bold transition-all ${paymentMethod === 'Cash' ? 'bg-white/20 text-white' : 'text-white/50'}`}>Cash</button>
                                <button onClick={() => setPaymentMethod('Online')} className={`flex-1 rounded-lg text-xs font-bold transition-all ${paymentMethod === 'Online' ? 'bg-white/20 text-white' : 'text-white/50'}`}>Online</button>
                            </div>
                        ) : (
                            <button onClick={handleHoldOrder} disabled={cart.length === 0} className="py-3 rounded-xl text-sm font-bold bg-yellow-500/20 text-yellow-400 border border-yellow-500/30 hover:bg-yellow-500/30 transition-all disabled:opacity-50">‚è∏Ô∏è Hold</button>
                        )}
                    </div>

                    <div className="flex gap-2 pt-1">
                        <button onClick={handlePrintBill} disabled={cart.length === 0} className="flex-1 bg-white/10 text-white/70 py-3.5 rounded-xl font-bold text-sm hover:bg-white/20 transition-all disabled:opacity-50 flex items-center justify-center gap-2">üñ®Ô∏è Bill</button>
                        <button onClick={handleCheckout} disabled={cart.length === 0 || isProcessing} className="flex-[2] bg-gradient-to-r from-orange-500 to-red-500 text-white py-3.5 rounded-xl font-bold text-lg shadow-lg shadow-orange-500/30 hover:shadow-orange-500/50 hover:scale-[1.02] transition-all disabled:opacity-50 flex items-center justify-center gap-2">
                            {isProcessing ? (
                                <><div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>Processing...</>
                            ) : (
                                <>Place Order üöÄ</>
                            )}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}
